.container {
  display: flex;
  flex-direction: column;
  row-gap: 44px;
  margin: 0 40px;
  margin-bottom: 40px;
}

.tabBody {
  display: flex;
  flex-direction: row;
  column-gap: 24px;
  align-items: flex-start;
}

.tab {
  display: flex;
  flex-direction: column;
  row-gap: 44px;
  width: calc((100% - 24px) / 2);
}
